@import url(https://fonts.googleapis.com/css2?family=Creepster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mystery+Quest&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}
h1{
  color: #ffdb5e;
  font-family: 'Creepster', cursive;
  letter-spacing: 10px;
  font-size: 40px;

}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.card-container {
    position: relative;
    display: flex;
    flex-direction: column;
    cursor: pointer;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    border-radius: 10px;
    box-shadow: 12px 24px rgba(0, 0, 0, 0.5), 0 4px 8px rgba(0, 0, 0, 0.3);
    background: linear-gradient(#f6921d, #b14623);
    background-repeat: no-repeat;
    text-align: center;
}
.card-container img {
    padding: 20px;
}
.card-container:hover {
    box-shadow: 12px 24px rgba(255, 255, 0, 0.2), 0 4px 8px rgba(255, 255, 0, 0.2);
    background: rgba(0, 0, 0, 0.5);
}
.card-container:hover img {
    opacity: 0.1;
}
.card-content {
    position: absolute;
    top: 25px;
    width: 100%;
    display: none;
}
.card-container:hover .card-content {
    display: block;
}
.card-container:hover .card-content p {
    color: white;
}
h3 {
    color: white;
    font-family: 'Mystery Quest', cursive;
    letter-spacing: 2px;
}

body {
    background: linear-gradient(#3e1c33, #130912);
    background-repeat: no-repeat;
}
.card-list {
    display: grid;
    grid-template-rows: auto;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    width: 85vw;
    grid-gap: 40px;
    margin: 50px auto;
}

.search{
    margin-top: 10px;
    width: 50%;
    height: 40px;
    border-radius: 30px;
    text-align: center;
    border: none;
    font-size: 18px;
    border: none;
}
.search:focus{
    outline: none;
}
